@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body,
#root {
  height: 100vh;
  margin: 0;
}
body,
input,
select,
button,
textarea {
  font-family: Montserrat, Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
}
a {
  text-decoration: none;
}
a:visited {
  color: blue;
}
.button {
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
  background-color: #f0f0f0;
  cursor: pointer;
  border-radius: 5%;
}
.button:hover {
  border: 0.1rem #808080 solid;
}
.button.primary {
  background-color: #f0c040;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 4rem;
    background-color: #1f3c88;
}
.navbar a {
    color: white;
    font-size: 2rem;
    font-weight: 300;
}
.navbar__logo {
    margin: 3rem;
}
.navbar__admin {
    margin: 3rem;
}
.products {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    justify-content: start;
    align-items: center;
    padding: 0;
    margin: 0;
}
.products li {
    flex: 0 1 29rem;
    height: 47rem;
    padding: 1rem;
}
.product {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}
.product img {
    max-width: 37rem;
    max-height: 37rem;
}
.product__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.product__price div,
.product__price button {
    text-align: center;
    flex: 1 1;
}
.product__price div {
    font-size: 2rem;
}

.product__details {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-height: 96vh;
}
.product__details img {
    max-height: 100vh;
    max-width: 40rem;
    margin: auto;
}
.product__detailsDescription {
    flex: 1 1 ;
    margin: 5rem;
}
.close__modal {
    position: absolute;
    right: 1rem;
    top: 2.5rem;
    z-index: 100;
}
.container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.main {
    flex: 3 1 70rem;
}
.shoppingCart {
    flex: 1 1 20rem;
}
.filter {
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    margin: 1rem;
    border-bottom: .1rem solid #c0c0c0;
}
.cart {
    padding:1.2rem;
    margin: 1rem;
    display: flex;  
}
.cart__header {
    border-bottom: .1rem solid #c0c0c0;
}
.cart__items {
    padding: 0;
    width: 100%;
    list-style-type: none;
}
.cart__items img {
    width: 5rem;
}
.cart__items li {
    display: flex;
}
.cart__items li div {
    padding: .5rem;
}
.cart__items li div:last-child {
    flex: 1 1;
}
.right {
    text-align: right;
}
.cart__total {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}
.cart__total > div {
    font-size: 2rem;
}
.cart__total > div,
.cart__total > button
 {
    flex: 1 1;
}
form {
    width: 100%;
}
.form__container {
    padding: 0;
    list-style-type: none;
    width: 100%;
}
.form__container li {
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
input {
    padding: 1rem;
    border: .1rem #c0c0c0 solid;
}
